// src/AdminItemPage.js
import React, { useState, useEffect } from "react";

// 50   
const iconCollection = {
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""],
  "": ["", "", "", "", "", "", "", "", "", "", "", ""]
};

const styles = {
  container: {
    padding: '20px',
    maxWidth: '1000px',
    margin: '0 auto',
    fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif'
  },
  card: {
    backgroundColor: '#ffffff',
    borderRadius: '16px',
    boxShadow: '0 4px 20px rgba(0, 0, 0, 0.08)',
    padding: '30px',
    marginBottom: '20px'
  },
  header: {
    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    color: 'white',
    padding: '30px',
    borderRadius: '16px 16px 0 0',
    marginBottom: '0'
  },
  title: {
    fontSize: '28px',
    fontWeight: 'bold',
    marginBottom: '8px'
  },
  subtitle: {
    opacity: '0.9',
    fontSize: '14px'
  },
  tabs: {
    display: 'flex',
    borderBottom: '2px solid #e5e7eb',
    backgroundColor: '#f9fafb',
    marginTop: '-4px'
  },
  tab: {
    flex: 1,
    padding: '16px',
    backgroundColor: 'transparent',
    border: 'none',
    fontSize: '16px',
    fontWeight: '600',
    color: '#6b7280',
    cursor: 'pointer',
    transition: 'all 0.3s ease',
    borderBottom: '3px solid transparent'
  },
  activeTab: {
    color: '#667eea',
    backgroundColor: 'white',
    borderBottom: '3px solid #667eea'
  },
  disabledTab: {
    opacity: '0.5',
    cursor: 'not-allowed'
  },
  formSection: {
    backgroundColor: '#f9fafb',
    padding: '20px',
    borderRadius: '12px',
    marginBottom: '20px'
  },
  formTitle: {
    fontSize: '20px',
    fontWeight: '700',
    color: '#374151',
    marginBottom: '20px'
  },
  inputGroup: {
    marginBottom: '20px'
  },
  label: {
    display: 'block',
    fontSize: '14px',
    fontWeight: '600',
    color: '#374151',
    marginBottom: '8px'
  },
  input: {
    width: '100%',
    padding: '12px',
    border: '2px solid #e5e7eb',
    borderRadius: '8px',
    fontSize: '14px',
    transition: 'border-color 0.3s ease',
    boxSizing: 'border-box'
  },
  textarea: {
    width: '100%',
    padding: '12px',
    border: '2px solid #e5e7eb',
    borderRadius: '8px',
    fontSize: '14px',
    minHeight: '100px',
    resize: 'vertical',
    boxSizing: 'border-box'
  },
  gridTwo: {
    display: 'grid',
    gridTemplateColumns: 'repeat(2, 1fr)',
    gap: '20px'
  },
  iconSelector: {
    backgroundColor: 'white',
    padding: '20px',
    borderRadius: '12px',
    border: '2px solid #e5e7eb'
  },
  selectedIcon: {
    fontSize: '48px',
    width: '80px',
    height: '80px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: '#f3f4f6',
    borderRadius: '12px',
    marginBottom: '16px'
  },
  button: {
    padding: '12px 24px',
    borderRadius: '8px',
    border: 'none',
    fontSize: '14px',
    fontWeight: '600',
    cursor: 'pointer',
    transition: 'all 0.3s ease'
  },
  primaryButton: {
    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    color: 'white'
  },
  secondaryButton: {
    backgroundColor: 'white',
    color: '#374151',
    border: '2px solid #e5e7eb'
  },
  iconGrid: {
    display: 'grid',
    gridTemplateColumns: 'repeat(12, 1fr)',
    gap: '8px',
    marginTop: '16px'
  },
  iconButton: {
    fontSize: '28px',
    padding: '8px',
    border: '2px solid transparent',
    borderRadius: '8px',
    backgroundColor: '#f9fafb',
    cursor: 'pointer',
    transition: 'all 0.2s ease'
  },
  categoryButtons: {
    display: 'flex',
    flexWrap: 'wrap',
    gap: '8px',
    marginBottom: '16px'
  },
  categoryButton: {
    padding: '8px 16px',
    borderRadius: '8px',
    border: '2px solid #e5e7eb',
    backgroundColor: 'white',
    fontSize: '13px',
    fontWeight: '500',
    cursor: 'pointer',
    transition: 'all 0.2s ease'
  },
  activeCategoryButton: {
    backgroundColor: '#667eea',
    color: 'white',
    borderColor: '#667eea'
  },
  priceIncreaseSection: {
    backgroundColor: '#fef3c7',
    border: '2px solid #fcd34d',
    padding: '20px',
    borderRadius: '12px',
    marginBottom: '20px'
  },
  helpText: {
    fontSize: '13px',
    color: '#6b7280',
    marginTop: '8px',
    lineHeight: '1.5'
  },
  checkboxWrapper: {
    display: 'flex',
    alignItems: 'center',
    gap: '12px'
  },
  checkbox: {
    width: '20px',
    height: '20px',
    cursor: 'pointer'
  },
  buttonGroup: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginTop: '30px',
    paddingTop: '20px',
    borderTop: '2px solid #e5e7eb'
  }
};

const AdminItemPage = ({
  classCode,
  editingItemFromStore,
  onAddItem,
  onUpdateItem,
  onClose,
}) => {
  const [activeTab, setActiveTab] = useState("addItem");
  const [showIconPicker, setShowIconPicker] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState("");
  const [item, setItem] = useState({
    name: "",
    description: "",
    price: "",
    stock: "",
    initialStock: "",
    icon: "",
    available: true,
    priceIncreasePercentage: "10", // []  priceIncreasePercentage 
  });

  useEffect(() => {
    if (editingItemFromStore) {
      setItem({ 
        ...editingItemFromStore,
        // []  priceIncreasePercentage ,    
        priceIncreasePercentage: editingItemFromStore.priceIncreasePercentage || "10"
      });
      setActiveTab("editItem");
    } else {
      setItem({
        name: "",
        description: "",
        price: "",
        stock: "",
        initialStock: "",
        icon: "",
        available: true,
        priceIncreasePercentage: "10", // []  priceIncreasePercentage 
      });
    }
  }, [editingItemFromStore]);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setItem((prev) => ({
      ...prev,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  const handleIconSelect = (icon) => {
    setItem(prev => ({ ...prev, icon }));
    setShowIconPicker(false);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const isEditing = !!(item && item.id);

    if (
      !item.name ||
      !item.price ||
      !item.stock ||
      isNaN(item.price) ||
      isNaN(item.stock) ||
      parseInt(item.price, 10) < 0 ||
      parseInt(item.stock, 10) < 0
    ) {
      alert(", ,   (0  ) .");
      return;
    }

    const itemData = {
      ...item,
      price: parseInt(item.price, 10),
      stock: parseInt(item.stock, 10),
      initialStock: item.initialStock
        ? parseInt(item.initialStock, 10)
        : parseInt(item.stock, 10),
      // []    priceIncreasePercentage ,  
      priceIncreasePercentage: parseFloat(item.priceIncreasePercentage) || 0,
      classCode,
    };

    let success = false;
    if (isEditing) {
      success = await onUpdateItem(itemData);
    } else {
      success = await onAddItem(itemData);
    }

    if (success) {
      if (!isEditing) {
        setItem({
          name: "",
          description: "",
          price: "",
          stock: "",
          initialStock: "",
          icon: "",
          available: true,
          priceIncreasePercentage: "10", // []  priceIncreasePercentage 
        });
        setActiveTab("addItem");
      }
    }
  };

  const handleTabClick = (tabName) => {
    setActiveTab(tabName);
    if (editingItemFromStore) {
      onClose();
    }
    if (tabName === 'addItem') {
      setItem({
        name: "",
        description: "",
        price: "",
        stock: "",
        initialStock: "",
        icon: "",
        available: true,
        priceIncreasePercentage: "10", // []  priceIncreasePercentage 
      });
    }
  };

  return (
    <div style={styles.container}>
      <div style={styles.card}>
        <div style={styles.header}>
          <h2 style={styles.title}>   </h2>
          <p style={styles.subtitle}>  </p>
        </div>

        <div style={styles.tabs}>
          <button
            style={{
              ...styles.tab,
              ...(activeTab === "addItem" ? styles.activeTab : {})
            }}
            onClick={() => handleTabClick("addItem")}
          >
              
          </button>
          <button
            style={{
              ...styles.tab,
              ...(activeTab === "editItem" ? styles.activeTab : {}),
              ...((!editingItemFromStore) ? styles.disabledTab : {})
            }}
            disabled={!editingItemFromStore}
            onClick={() => setActiveTab("editItem")}
          >
              
          </button>
        </div>

        <div style={{ padding: '30px' }}>
          {(activeTab === "addItem" || activeTab === "editItem") && (
            <form onSubmit={handleSubmit}>
              <div style={styles.formSection}>
                <h3 style={styles.formTitle}>
                  {activeTab === "editItem" ? "  " : "  "}
                </h3>

                {/*   */}
                <div style={styles.iconSelector}>
                  <label style={styles.label}> </label>
                  <div style={{ display: 'flex', alignItems: 'center', gap: '16px' }}>
                    <div style={styles.selectedIcon}>
                      {item.icon}
                    </div>
                    <button
                      type="button"
                      onClick={() => setShowIconPicker(!showIconPicker)}
                      style={{ ...styles.button, ...styles.primaryButton }}
                    >
                       
                    </button>
                  </div>

                  {showIconPicker && (
                    <div style={{ 
                      marginTop: '20px', 
                      padding: '16px', 
                      backgroundColor: '#f9fafb', 
                      borderRadius: '8px', 
                      width: '100%', 
                      boxSizing: 'border-box',
                      maxHeight: '400px',
                      overflowY: 'auto'
                    }}>
                      <div style={styles.categoryButtons}>
                        {Object.keys(iconCollection).map((category) => (
                          <button
                            key={category}
                            type="button"
                            onClick={() => setSelectedCategory(category)}
                            style={{
                              ...styles.categoryButton,
                              ...(selectedCategory === category ? styles.activeCategoryButton : {})
                            }}
                          >
                            {category}
                          </button>
                        ))}
                      </div>
                      <div style={styles.iconGrid}>
                        {iconCollection[selectedCategory].map((icon) => (
                          <button
                            key={icon}
                            type="button"
                            onClick={() => handleIconSelect(icon)}
                            style={styles.iconButton}
                            onMouseEnter={(e) => {
                              e.target.style.backgroundColor = '#e5e7eb';
                              e.target.style.transform = 'scale(1.1)';
                            }}
                            onMouseLeave={(e) => {
                              e.target.style.backgroundColor = '#f9fafb';
                              e.target.style.transform = 'scale(1)';
                            }}
                          >
                            {icon}
                          </button>
                        ))}
                      </div>
                    </div>
                  )}
                </div>

                {/*   */}
                <div style={styles.gridTwo}>
                  <div style={styles.inputGroup}>
                    <label style={styles.label}>
                       <span style={{ color: 'red' }}>*</span>
                    </label>
                    <input
                      name="name"
                      value={item.name}
                      onChange={handleChange}
                      placeholder=":  "
                      required
                      style={styles.input}
                    />
                  </div>

                  <div style={styles.inputGroup}>
                    <label style={styles.label}>
                        () <span style={{ color: 'red' }}>*</span>
                    </label>
                    <input
                      name="price"
                      type="number"
                      value={item.price}
                      onChange={handleChange}
                      placeholder=": 1000"
                      required
                      min="0"
                      style={styles.input}
                    />
                  </div>

                  <div style={styles.inputGroup}>
                    <label style={styles.label}>
                        <span style={{ color: 'red' }}>*</span>
                    </label>
                    <input
                      name="stock"
                      type="number"
                      value={item.stock}
                      onChange={handleChange}
                      placeholder=": 10"
                      required
                      min="0"
                      style={styles.input}
                    />
                  </div>

                  <div style={styles.inputGroup}>
                    <label style={styles.label}>
                        (  )
                    </label>
                    <input
                      name="initialStock"
                      type="number"
                      value={item.initialStock}
                      onChange={handleChange}
                      placeholder="   "
                      min="0"
                      style={styles.input}
                    />
                  </div>
                </div>

                {/*   */}
                <div style={styles.priceIncreaseSection}>
                  <label style={styles.label}>
                          (%)
                  </label>
                  <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
                    <input
                      name="priceIncreasePercentage" // [] input name 
                      type="number"
                      value={item.priceIncreasePercentage} // [] value 
                      onChange={handleChange}
                      placeholder="10"
                      min="0"
                      max="100"
                      step="0.5"
                      style={{ ...styles.input, width: '120px' }}
                    />
                    <span style={{ fontSize: '16px', color: '#374151' }}>%</span>
                  </div>
                  <p style={styles.helpText}>
                      0          .<br />
                    : 10%  , 1000  1100  1210  
                  </p>
                </div>

                {/*   */}
                <div style={styles.inputGroup}>
                  <label style={styles.label}> </label>
                  <textarea
                    name="description"
                    value={item.description}
                    onChange={handleChange}
                    placeholder="    ."
                    style={styles.textarea}
                  />
                </div>
              </div>

              {/*    */}
              <div style={styles.buttonGroup}>
                <div style={styles.checkboxWrapper}>
                  <input
                    type="checkbox"
                    name="available"
                    checked={item.available}
                    onChange={handleChange}
                    style={styles.checkbox}
                  />
                  <label style={{ fontSize: '16px', fontWeight: '500' }}>
                    {item.available ? " " : " "}
                  </label>
                </div>

                <div style={{ display: 'flex', gap: '12px' }}>
                  {activeTab === "editItem" && (
                    <button
                      type="button"
                      onClick={() => handleTabClick("addItem")}
                      style={{ ...styles.button, ...styles.secondaryButton }}
                    >
                      
                    </button>
                  )}
                  <button
                    type="submit"
                    style={{ ...styles.button, ...styles.primaryButton }}
                  >
                    {activeTab === "editItem" ? "  " : "  "}
                  </button>
                </div>
              </div>
            </form>
          )}
        </div>
      </div>
    </div>
  );
};

export default AdminItemPage;