//   
export const realtimeOptimizer = {
  //     
  shouldUseRealtime: () => {
    return navigator.onLine && !document.hidden;
  },

  //     
  getOptimalCacheTime: (dataSize) => {
    if (dataSize < 1000) return 10 * 60 * 1000; // 10
    if (dataSize < 5000) return 5 * 60 * 1000;  // 5
    return 2 * 60 * 1000; // 2
  },
};

//    ( )
export const localBackup = {
  save: (key, data) => {
    try {
      localStorage.setItem(`backup_${key}`, JSON.stringify({
        data,
        timestamp: Date.now(),
      }));
    } catch (error) {
      logger.warn("   :", error);
    }
  },

  load: (key, maxAge = 60000) => { //  1
    try {
      const backup = localStorage.getItem(`backup_${key}`);
      if (backup) {
        const parsed = JSON.parse(backup);
        if (Date.now() - parsed.timestamp < maxAge) {
          return parsed.data;
        }
      }
    } catch (error) {
      logger.warn("   :", error);
    }
    return null;
  },

  clear: (key) => {
    try {
      localStorage.removeItem(`backup_${key}`);
    } catch (error) {
      logger.warn("   :", error);
    }
  },
};