/**
 *     ()
 *  : classCode       
 * @param {string} classCode -  
 * @param {string} userId -  ID ()
 * @returns {Promise<object>}
 */
export const getActivitySummary = async (classCode, userId = null) => {
  try {
    const cacheKey = getCacheKey(classCode, 'activity_summary', { userId });
    const cached = getFromCache(cacheKey);
    if (cached) {
      logger.log('[AdminDatabaseService]    ');
      return cached;
    }

    logger.log('[AdminDatabaseService]     ( ):', { classCode, userId });

    //  : classCode   
    const constraints = [where('classCode', '==', classCode)];

    if (userId) {
      constraints.push(where('userId', '==', userId));
    }

    //  1000   
    constraints.push(orderBy('timestamp', 'desc'));
    constraints.push(limit(1000));

    const baseQuery = query(collection(db, 'activity_logs'), ...constraints);

    //    
    const snapshot = await getDocs(baseQuery);

    logger.log(`[AdminDatabaseService]   : ${snapshot.size} (classCode  )`);

    const summary = {
      totalActivities: 0,
      couponEarned: 0,
      couponUsed: 0,
      itemPurchased: 0,
      itemUsed: 0,
      itemSold: 0,
      byUser: {}
    };

    snapshot.forEach(doc => {
      const data = doc.data();
      const normalizedType = normalizeType(data.type); //  

      summary.totalActivities++;

      //  
      if (!summary.byUser[data.userId]) {
        summary.byUser[data.userId] = {
          userName: data.userName || '  ',
          couponEarned: 0,
          couponUsed: 0,
          itemPurchased: 0,
          itemUsed: 0,
          itemSold: 0,
          activities: []
        };
      }

      const userSummary = summary.byUser[data.userId];

      //   (  )
      switch (normalizedType) {
        case 'COUPON_EARN':
          const earnAmount = data.metadata?.couponAmount || 1; //  1
          summary.couponEarned += earnAmount;
          userSummary.couponEarned += earnAmount;
          break;
        case 'COUPON_USE':
          const useAmount = data.metadata?.couponAmount || 1; //  1
          summary.couponUsed += useAmount;
          userSummary.couponUsed += useAmount;
          break;
        case 'ITEM_PURCHASE':
          summary.itemPurchased++;
          userSummary.itemPurchased++;
          break;
        case 'ITEM_USE':
          summary.itemUsed++;
          userSummary.itemUsed++;
          break;
        case 'ITEM_SELL':
        case 'ITEM_MARKET_LIST':
          summary.itemSold++;
          userSummary.itemSold++;
          break;
        default:
          break;
      }

      //    ( 10)
      if (userSummary.activities.length < 10) {
        userSummary.activities.push({
          type: normalizedType, //   
          description: data.description,
          timestamp: data.timestamp?.toDate() || new Date(),
          metadata: data.metadata
        });
      }
    });

    logger.log(`[AdminDatabaseService]    :`, {
      totalActivities: summary.totalActivities,
      couponEarned: summary.couponEarned,
      couponUsed: summary.couponUsed,
      itemPurchased: summary.itemPurchased,
      itemUsed: summary.itemUsed,
      itemSold: summary.itemSold,
      userCount: Object.keys(summary.byUser).length
    });

    //  
    setCache(cacheKey, summary);

    return summary;

  } catch (error) {
    logger.error('[AdminDatabaseService]    :', error);
    logger.error('[AdminDatabaseService]  :', error.message, error.code);
    throw error;
  }
};

/**
 *  
 */
export const clearCache = () => {
  cache.clear();
  logger.log('[AdminDatabaseService]   ');
};

/**
 *    
 */
export const clearClassCache = (classCode) => {
  const keysToDelete = [];
  cache.forEach((value, key) => {
    if (key.startsWith(classCode)) {
      keysToDelete.push(key);
    }
  });
  keysToDelete.forEach(key => cache.delete(key));
  logger.log(`[AdminDatabaseService] ${classCode}    `);
};

/**
 * transactions    ()
 * @param {Array} userIds -  ID 
 * @param {object} options -  
 * @returns {Promise<Array>}
 */
export const getTransactionsData = async (userIds = [], options = {}) => {
  try {
    const { limitCount = 100 } = options;

    logger.log('[AdminDatabaseService] transactions   :', { userIdsCount: userIds.length });

    if (userIds.length === 0) {
      logger.warn('[AdminDatabaseService] userIds ');
      return [];
    }

    // Firestore 'in'   10   
    const batches = [];
    for (let i = 0; i < userIds.length; i += 10) {
      batches.push(userIds.slice(i, i + 10));
    }

    const allTransactions = [];

    for (const batch of batches) {
      let q = query(
        collection(db, 'transactions'),
        where('userId', 'in', batch),
        orderBy('timestamp', 'desc'),
        limit(limitCount)
      );

      const snapshot = await getDocs(q);

      snapshot.forEach(doc => {
        const data = doc.data();
        allTransactions.push({
          id: doc.id,
          ...data,
          timestamp: data.timestamp?.toDate() || new Date()
        });
      });
    }

    logger.log(`[AdminDatabaseService] transactions  : ${allTransactions.length}`);
    return allTransactions;

  } catch (error) {
    logger.error('[AdminDatabaseService] transactions  :', error);
    return [];
  }
};

/**
 * transactions  activity_logs  
 * @param {Array} transactions -  
 * @returns {Array}
 */
export const convertTransactionsToLogs = (transactions) => {
  return transactions.map(tx => {
    let type = 'CASH_INCOME';
    let description = tx.description || '';

    //    
    if (tx.description) {
      if (tx.description.includes('') && tx.description.includes('')) {
        type = 'COUPON_EARN';
      } else if (tx.description.includes('') && tx.description.includes('')) {
        type = 'COUPON_USE';
      } else if (tx.description.includes('') && tx.description.includes('')) {
        type = 'ITEM_PURCHASE';
      } else if (tx.description.includes('') && tx.description.includes('')) {
        type = 'ITEM_USE';
      } else if (tx.description.includes('') && (tx.description.includes('') || tx.description.includes(''))) {
        type = 'ITEM_SELL';
      } else if (tx.description.includes('')) {
        type = tx.amount > 0 ? 'CASH_TRANSFER_RECEIVE' : 'CASH_TRANSFER_SEND';
      } else if (tx.type === 'expense') {
        type = 'CASH_EXPENSE';
      }
    }

    return {
      id: tx.id,
      userId: tx.userId,
      userName: tx.userName || '  ',
      type,
      description,
      timestamp: tx.timestamp,
      metadata: {
        amount: tx.amount,
        category: tx.category,
        ...tx.metadata
      }
    };
  });
};
