// src/AdminDatabase.js
import React, { useState, useEffect, useCallback } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import {
  getActivityLogs,
  clearClassCache,
  getTransactionsData,
  convertTransactionsToLogs
} from '../../services/AdminDatabaseService';
import './AdminDatabase.css';
import { logger } from '../../utils/logger';

const AdminDatabase = () => {
  const { userDoc, allClassMembers } = useAuth();
  const [selectedUser, setSelectedUser] = useState('all');
  const [activityData, setActivityData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [hasMore, setHasMore] = useState(false);
  const [lastDoc, setLastDoc] = useState(null);
  const [debugInfo, setDebugInfo] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [activityTypeFilter, setActivityTypeFilter] = useState('all');

  const classCode = userDoc?.classCode;

  const loadData = useCallback(async (refresh = false, lastDocForPagination = null) => {
    if (!classCode || classCode === '') {
      setError('  .');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      //  :        
      const isFiltered = activityTypeFilter !== 'all' || searchTerm.trim() !== '';
      const limitCount = isFiltered ? 100 : 10; //    100,  10

      const options = {
        userId: selectedUser !== 'all' ? selectedUser : null,
        limitCount: limitCount,
        lastDoc: refresh ? null : lastDocForPagination,
        useCache: !refresh
      };

      const result = await getActivityLogs(classCode, options);

      if (result.logs.length === 0 && allClassMembers.length > 0 && refresh) {
        const userIds = selectedUser !== 'all'
          ? [selectedUser]
          : allClassMembers.map(m => m.id || m.uid);
        const transactions = await getTransactionsData(userIds, { limitCount: 100 });

        if (transactions.length > 0) {
          result.logs = convertTransactionsToLogs(transactions);
          result.hasMore = false;
        }
      }

      if (refresh) {
        setActivityData(result.logs);
      } else {
        setActivityData(prev => [...prev, ...result.logs]);
      }
      setHasMore(result.hasMore);
      setLastDoc(result.lastDoc);

    } catch (err) {
      logger.error('[AdminDatabase]   :', err);
      setError('    .');
    } finally {
      setLoading(false);
    }
  }, [classCode, selectedUser, allClassMembers, activityTypeFilter, searchTerm]);

  useEffect(() => {
    setActivityData([]);
    setLastDoc(null);
    setHasMore(false);
    loadData(true, null);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedUser, classCode]); // loadData    

  //       
  useEffect(() => {
    if (activityData.length > 0) {
      setActivityData([]);
      setLastDoc(null);
      setHasMore(false);
      loadData(true, null);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activityTypeFilter, searchTerm]); // loadData activityData    

  const handleRefresh = () => {
    clearClassCache(classCode);
    setActivityData([]);
    setLastDoc(null);
    setHasMore(false);
    setDebugInfo('');
    loadData(true, null);
  };

  const handleDebugFirestore = async () => {
    setDebugInfo(' ...');
    try {
      let info = [];

      //     
      const typeCounts = {};
      const typeExamples = {};

      activityData.forEach(log => {
        const type = log.type || 'undefined';
        typeCounts[type] = (typeCounts[type] || 0) + 1;

        if (!typeExamples[type]) {
          typeExamples[type] = {
            description: log.description,
            userName: log.userName,
            timestamp: log.timestamp
          };
        }
      });

      info.push('===      ===');
      info.push(` ${activityData.length} `);
      info.push('');

      Object.entries(typeCounts)
        .sort((a, b) => b[1] - a[1])
        .forEach(([type, count]) => {
          info.push(`${type}: ${count}`);
          if (typeExamples[type]) {
            info.push(`  : ${typeExamples[type].description}`);
          }
          //     
          if (type.includes('COUPON') || type.includes('') ||
              type.includes('ITEM') || type.includes('')) {
            info.push(`   /   !`);
          }
        });

      info.push('');
      info.push('===    ===');

      //      
      const filters = {
        'coupon': (log) => {
          const type = log.type?.toString() || '';
          const desc = log.description?.toString() || '';
          const couponTypes = [
            ' ', ' ', ' ', ' ',
            ' ', ' ', ' ', ' ',
            'COUPON_EARN', 'COUPON_USE', 'COUPON_GIVE', 'COUPON_TAKE',
            'COUPON_TRANSFER_SEND', 'COUPON_TRANSFER_RECEIVE',
            'COUPON_DONATE', 'COUPON_SELL'
          ];
          return couponTypes.some(t => type === t) ||
                 type.includes('') ||
                 desc.includes('');
        },
        'item': (log) => {
          const type = log.type?.toString() || '';
          const desc = log.description?.toString() || '';
          const itemTypes = [
            ' ', ' ', ' ',
            '  ', '  ', ' ', ' ',
            'ITEM_PURCHASE', 'ITEM_USE', 'ITEM_SELL',
            'ITEM_MARKET_LIST', 'ITEM_MARKET_BUY', 'ITEM_OBTAIN', 'ITEM_MOVE'
          ];
          return itemTypes.some(t => type === t) ||
                 type.includes('') ||
                 desc.includes('');
        },
        'cash': (log) => {
          const type = log.type?.toString() || '';
          return type.includes('CASH') || type.includes('') ||
                 type.includes('') || type.includes('') ||
                 type.includes('ADMIN_CASH') || type.includes('TRANSFER') ||
                 type.includes('');
        },
        'stock': (log) => {
          const type = log.type?.toString() || '';
          return type.includes('STOCK') || type.includes('') ||
                 type.includes('') || type.includes('TAX');
        },
        'game': (log) => {
          const type = log.type?.toString() || '';
          return type.includes('GAME') || type.includes('');
        }
      };

      Object.entries(filters).forEach(([filterName, filterFunc]) => {
        const matchedLogs = activityData.filter(log => filterFunc(log));
        info.push(`${filterName}: ${matchedLogs.length}`);

        const matchedTypes = new Set(matchedLogs.map(log => log.type));
        if (matchedTypes.size > 0) {
          info.push(`   : ${Array.from(matchedTypes).join(', ')}`);
        }
      });

      const debugText = info.join('\n');
      setDebugInfo(debugText);

    } catch (error) {
      logger.error('[AdminDatabase DEBUG] :', error);
      setDebugInfo(`: ${error.message}`);
    }
  };

  const handleLoadMore = () => {
    if (!loading && hasMore) {
      loadData(false, lastDoc);
    }
  };

  const getActivityTypeText = (type) => {
    const typeMap = {
      ' ': ' ',
      ' ': ' ',
      ' ': ' ',
      ' ': ' ',
      ' ': ' ',
      ' ': ' ',
      ' ': ' ',
      '  ': '  ',
      '  ': '  ',
      ' ': ' ',
      ' ': ' ',
      ' ': ' ',
      '': ' ',
      '': '',
      ' ': ' ',
      ' ': '',
      ' ': '',
      ' ': '',
      ' ': '',
      ' ': '',
      'COUPON_EARN': ' ',
      'COUPON_USE': ' ',
      'COUPON_GIVE': ' ',
      'COUPON_TAKE': ' ',
      'COUPON_TRANSFER_SEND': ' ',
      'COUPON_TRANSFER_RECEIVE': ' ',
      'ITEM_PURCHASE': ' ',
      'ITEM_USE': ' ',
      'ITEM_SELL': ' ',
      'ITEM_MARKET_LIST': '  ',
      'ITEM_MARKET_BUY': '  ',
      'CASH_TRANSFER_SEND': '',
      'CASH_TRANSFER_RECEIVE': ' ',
      'CASH_WITHDRAW': '',
      'CASH_DEPOSIT': '',
      'STOCK_BUY': ' ',
      'STOCK_SELL': ' ',
      'STOCK_TAX': ' ',
      'TAX_PAYMENT': ' ',
      'ADMIN_CASH_SEND': ' ',
      'ADMIN_CASH_TAKE': ' ',
      'ADMIN_COUPON_GIVE': '  ',
      'ADMIN_COUPON_TAKE': '  ',
      'TASK_COMPLETE': ' ',
      'GAME_WIN': ' ',
      'GAME_LOSE': ' ',
    };
    return typeMap[type] || type;
  };

  const getActivityTypeColor = (type) => {
    if (!type) return 'text-gray-600';
    const typeStr = type.toString().toLowerCase();
    if (typeStr.includes('') && (typeStr.includes('') || typeStr.includes(''))) return 'text-green-600';
    if (typeStr.includes('') && (typeStr.includes('') || typeStr.includes(''))) return 'text-red-600';
    if (type === 'COUPON_EARN' || type === 'COUPON_GIVE' || type === 'COUPON_TRANSFER_RECEIVE' || type === 'ADMIN_COUPON_GIVE') return 'text-green-600';
    if (type === 'COUPON_USE' || type === 'COUPON_TAKE' || type === 'COUPON_TRANSFER_SEND' || type === 'ADMIN_COUPON_TAKE') return 'text-red-600';
    if (typeStr.includes('') && typeStr.includes('')) return 'text-blue-600';
    if (typeStr.includes('') && typeStr.includes('')) return 'text-orange-600';
    if (typeStr.includes('') && (typeStr.includes('') || typeStr.includes(''))) return 'text-purple-600';
    if (type === 'ITEM_PURCHASE' || type === 'ITEM_MARKET_BUY') return 'text-blue-600';
    if (type === 'ITEM_USE') return 'text-orange-600';
    if (type === 'ITEM_SELL' || type === 'ITEM_MARKET_LIST') return 'text-purple-600';
    if (typeStr.includes('') || typeStr.includes('') || typeStr.includes('')) return 'text-green-600';
    if (typeStr.includes('') || typeStr.includes('') || typeStr.includes('')) return 'text-red-600';
    if (type === 'ADMIN_CASH_SEND' || type === 'CASH_TRANSFER_RECEIVE' || type === 'CASH_DEPOSIT' || type === ' ') return 'text-green-600';
    if (type === 'ADMIN_CASH_TAKE' || type === 'CASH_TRANSFER_SEND' || type === 'CASH_WITHDRAW' || type === ' ') return 'text-red-600';
    if (typeStr.includes('')) return 'text-blue-600';
    if (typeStr.includes('')) return 'text-indigo-600';
    if (typeStr.includes('') || typeStr.includes('')) return 'text-yellow-600';
    if (type === 'STOCK_BUY') return 'text-blue-600';
    if (type === 'STOCK_SELL') return 'text-indigo-600';
    if (type === 'STOCK_TAX' || type === 'TAX_PAYMENT') return 'text-yellow-600';
    if (type === 'GAME_WIN') return 'text-green-600';
    if (type === 'GAME_LOSE') return 'text-red-600';
    return 'text-gray-600';
  };

  const formatDate = (date) => {
    if (!date) return '-';
    const d = date instanceof Date ? date : new Date(date);
    return d.toLocaleString('ko-KR', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const filteredActivityData = activityData.filter(log => {
    //  
    if (searchTerm) {
      const term = searchTerm.toLowerCase();
      const matchesSearch = (
        log.description?.toLowerCase().includes(term) ||
        log.userName?.toLowerCase().includes(term) ||
        log.type?.toLowerCase().includes(term)
      );
      if (!matchesSearch) return false;
    }

    //   
    if (activityTypeFilter !== 'all') {
      const logType = log.type?.toString() || '';
      const logDesc = log.description?.toString() || '';

      if (activityTypeFilter === 'coupon') {
        //   - Functions   
        const couponTypes = [
          ' ', ' ', ' ', ' ',
          ' ', ' ', ' ', ' ',
          'COUPON_EARN', 'COUPON_USE', 'COUPON_GIVE', 'COUPON_TAKE',
          'COUPON_TRANSFER_SEND', 'COUPON_TRANSFER_RECEIVE',
          'COUPON_DONATE', 'COUPON_SELL'
        ];
        const isCouponType = couponTypes.some(type => logType === type) ||
                            logType.includes('') ||
                            logDesc.includes('');
        if (!isCouponType) return false;
      } else if (activityTypeFilter === 'item') {
        //   - Functions   
        const itemTypes = [
          ' ', ' ', ' ',
          '  ', '  ', ' ', ' ',
          'ITEM_PURCHASE', 'ITEM_USE', 'ITEM_SELL',
          'ITEM_MARKET_LIST', 'ITEM_MARKET_BUY', 'ITEM_OBTAIN', 'ITEM_MOVE'
        ];
        const isItemType = itemTypes.some(type => logType === type) ||
                          logType.includes('') ||
                          logDesc.includes('');
        if (!isItemType) return false;
      } else if (activityTypeFilter === 'cash') {
        //  
        const isCashType = logType.includes('CASH') || logType.includes('') ||
                          logType.includes('') || logType.includes('') ||
                          logType.includes('ADMIN_CASH') || logType.includes('TRANSFER') ||
                          logType.includes('');
        if (!isCashType) return false;
      } else if (activityTypeFilter === 'stock') {
        //  
        const isStockType = logType.includes('STOCK') || logType.includes('') ||
                           logType.includes('') || logType.includes('TAX');
        if (!isStockType) return false;
      } else if (activityTypeFilter === 'game') {
        //  
        const isGameType = logType.includes('GAME') || logType.includes('');
        if (!isGameType) return false;
      }
    }

    return true;
  });

  const renderActivityLogs = () => {
    if (filteredActivityData.length === 0) {
      return (
        <div className="text-center py-12 text-gray-500">
          <p className="text-lg mb-2">{searchTerm ? '  .' : '  .'}</p>
          <p className="text-sm"> : {activityData.length}</p>
        </div>
      );
    }

    return (
      <div className="space-y-4">
        <div className="text-sm text-gray-600 mb-4">
           {filteredActivityData.length}  (: {activityData.length} )
        </div>

        {filteredActivityData.map((log, index) => {
          return (
            <div key={log.id || index} className="bg-white border-2 border-gray-300 rounded-lg p-4 hover:shadow-lg transition-shadow">
              <div className="flex justify-between items-start">
                <div className="flex-1">
                  <div className="flex items-center gap-3 mb-2">
                    <span className={`font-bold text-base ${getActivityTypeColor(log.type)}`}>
                      {getActivityTypeText(log.type)}
                    </span>
                    <span className="text-sm font-medium text-gray-700">{log.userName || '  '}</span>
                    <span className="text-xs text-gray-500">{formatDate(log.timestamp)}</span>
                  </div>
                  <p className="text-gray-800 text-sm mb-2 font-medium">{log.description || ' '}</p>
                  {log.metadata && Object.keys(log.metadata).length > 0 && (
                    <div className="bg-gray-50 p-3 rounded text-sm text-gray-700 border border-gray-200">
                      {log.metadata.activity && (
                        <div className="mb-1"><strong>:</strong> {log.metadata.activity}</div>
                      )}
                      {log.metadata.reason && (
                        <div className="mb-1"><strong>:</strong> {log.metadata.reason}</div>
                      )}
                      {log.metadata.itemName && (
                        <div className="mb-1"><strong>:</strong> {log.metadata.itemName}</div>
                      )}
                      {log.metadata.quantity && (
                        <div className="mb-1"><strong>:</strong> {log.metadata.quantity}</div>
                      )}
                      {log.metadata.amount !== undefined && (
                        <div className="mb-1"><strong>:</strong> {log.metadata.amount.toLocaleString()}</div>
                      )}
                      {log.metadata.couponAmount !== undefined && (
                        <div className="mb-1"><strong>:</strong> {log.metadata.couponAmount}</div>
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          );
        })}

        {hasMore && (
          <div className="text-center py-4">
            <button
              onClick={handleLoadMore}
              disabled={loading}
              className="px-6 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:bg-gray-300"
            >
              {loading ? ' ...' : ''}
            </button>
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="admin-database p-6">
      <div className="mb-6">
        <h2 className="text-2xl font-bold text-gray-800 mb-2"> </h2>
        <p className="text-gray-600">       .</p>
      </div>

      {/*    */}
      <div className="bg-white p-4 rounded-lg shadow-sm mb-6">
        <div className="flex flex-wrap gap-4 items-center">
          {/*   */}
          <div className="flex-1 min-w-[200px]">
            <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
            <select
              value={selectedUser}
              onChange={(e) => setSelectedUser(e.target.value)}
              className="w-full p-2 border rounded-md"
            >
              <option value="all"> </option>
              {allClassMembers && allClassMembers.map(member => (
                <option key={member.id || member.uid} value={member.id || member.id}>
                  {member.name || member.nickname || ' '}
                </option>
              ))}
            </select>
          </div>

          {/*    */}
          <div className="flex-1 min-w-[200px]">
            <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
            <select
              value={activityTypeFilter}
              onChange={(e) => setActivityTypeFilter(e.target.value)}
              className="w-full p-2 border rounded-md"
            >
              <option value="all"> </option>
              <option value="coupon"> </option>
              <option value="item"> </option>
              <option value="cash"> </option>
              <option value="stock"> </option>
              <option value="game"> </option>
            </select>
          </div>

          {/*  */}
          <div className="flex-1 min-w-[200px]">
            <label className="block text-sm font-medium text-gray-700 mb-1"></label>
            <input
              type="text"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              placeholder=",   ..."
              className="w-full p-2 border rounded-md"
            />
          </div>

          {/*   */}
          <div className="flex items-end gap-2">
            <button
              onClick={handleRefresh}
              disabled={loading}
              className="px-4 py-2 bg-green-500 text-white rounded-md hover:bg-green-600 disabled:bg-gray-300"
            >
              
            </button>
            <button
              onClick={handleDebugFirestore}
              className="px-4 py-2 bg-yellow-500 text-white rounded-md hover:bg-yellow-600"
            >
              
            </button>
          </div>
        </div>

        {/*    */}
        {debugInfo && (
          <div className="mt-4 p-4 bg-gray-100 rounded-md">
            <h4 className="font-semibold mb-2"> :</h4>
            <pre className="text-xs whitespace-pre-wrap">{debugInfo}</pre>
          </div>
        )}
      </div>

      {/*   */}
      {activityData.length > 0 && (() => {
        const couponCount = activityData.filter(log => {
          const type = log.type?.toString() || '';
          const desc = log.description?.toString() || '';
          const couponTypes = [
            ' ', ' ', ' ', ' ',
            ' ', ' ', ' ', ' ',
            'COUPON_EARN', 'COUPON_USE', 'COUPON_GIVE', 'COUPON_TAKE',
            'COUPON_TRANSFER_SEND', 'COUPON_TRANSFER_RECEIVE',
            'COUPON_DONATE', 'COUPON_SELL'
          ];
          return couponTypes.some(t => type === t) ||
                 type.includes('') ||
                 desc.includes('');
        }).length;

        const itemCount = activityData.filter(log => {
          const type = log.type?.toString() || '';
          const desc = log.description?.toString() || '';
          const itemTypes = [
            ' ', ' ', ' ',
            '  ', '  ', ' ', ' ',
            'ITEM_PURCHASE', 'ITEM_USE', 'ITEM_SELL',
            'ITEM_MARKET_LIST', 'ITEM_MARKET_BUY', 'ITEM_OBTAIN', 'ITEM_MOVE'
          ];
          return itemTypes.some(t => type === t) ||
                 type.includes('') ||
                 desc.includes('');
        }).length;

        const cashCount = activityData.filter(log => {
          const type = log.type?.toString() || '';
          return type.includes('CASH') || type.includes('') ||
                 type.includes('') || type.includes('') ||
                 type.includes('ADMIN_CASH') || type.includes('TRANSFER');
        }).length;

        const stockCount = activityData.filter(log => {
          const type = log.type?.toString() || '';
          return type.includes('STOCK') || type.includes('') ||
                 type.includes('') || type.includes('TAX');
        }).length;

        const gameCount = activityData.filter(log => {
          const type = log.type?.toString() || '';
          return type.includes('GAME') || type.includes('');
        }).length;

        return (
          <div className="bg-white p-4 rounded-lg shadow-sm mb-6">
            <h3 className="text-lg font-semibold text-gray-800 mb-3">  ( )</h3>
            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-4">
              <div
                className="bg-blue-50 p-3 rounded cursor-pointer hover:shadow-md transition-shadow"
                onClick={() => setActivityTypeFilter('all')}
              >
                <div className="text-xs text-gray-600 mb-1"> </div>
                <div className="text-xl font-bold text-blue-600">
                  {activityData.length}
                </div>
              </div>
              <div
                className={`p-3 rounded transition-shadow ${
                  couponCount > 0
                    ? 'bg-green-50 cursor-pointer hover:shadow-md'
                    : 'bg-gray-100 cursor-not-allowed opacity-50'
                }`}
                onClick={() => couponCount > 0 && setActivityTypeFilter('coupon')}
                title={couponCount === 0 ? '    ' : '    '}
              >
                <div className="text-xs text-gray-600 mb-1"> </div>
                <div className={`text-xl font-bold ${couponCount > 0 ? 'text-green-600' : 'text-gray-400'}`}>
                  {couponCount}
                </div>
              </div>
              <div
                className={`p-3 rounded transition-shadow ${
                  itemCount > 0
                    ? 'bg-purple-50 cursor-pointer hover:shadow-md'
                    : 'bg-gray-100 cursor-not-allowed opacity-50'
                }`}
                onClick={() => itemCount > 0 && setActivityTypeFilter('item')}
                title={itemCount === 0 ? '    ' : '    '}
              >
                <div className="text-xs text-gray-600 mb-1"> </div>
                <div className={`text-xl font-bold ${itemCount > 0 ? 'text-purple-600' : 'text-gray-400'}`}>
                  {itemCount}
                </div>
              </div>
              <div
                className={`p-3 rounded transition-shadow ${
                  cashCount > 0
                    ? 'bg-yellow-50 cursor-pointer hover:shadow-md'
                    : 'bg-gray-100 cursor-not-allowed opacity-50'
                }`}
                onClick={() => cashCount > 0 && setActivityTypeFilter('cash')}
                title={cashCount === 0 ? '    ' : '    '}
              >
                <div className="text-xs text-gray-600 mb-1"> </div>
                <div className={`text-xl font-bold ${cashCount > 0 ? 'text-yellow-600' : 'text-gray-400'}`}>
                  {cashCount}
                </div>
              </div>
              <div
                className={`p-3 rounded transition-shadow ${
                  stockCount > 0
                    ? 'bg-indigo-50 cursor-pointer hover:shadow-md'
                    : 'bg-gray-100 cursor-not-allowed opacity-50'
                }`}
                onClick={() => stockCount > 0 && setActivityTypeFilter('stock')}
                title={stockCount === 0 ? '    ' : '    '}
              >
                <div className="text-xs text-gray-600 mb-1"> </div>
                <div className={`text-xl font-bold ${stockCount > 0 ? 'text-indigo-600' : 'text-gray-400'}`}>
                  {stockCount}
                </div>
              </div>
              <div
                className={`p-3 rounded transition-shadow ${
                  gameCount > 0
                    ? 'bg-red-50 cursor-pointer hover:shadow-md'
                    : 'bg-gray-100 cursor-not-allowed opacity-50'
                }`}
                onClick={() => gameCount > 0 && setActivityTypeFilter('game')}
                title={gameCount === 0 ? '    ' : '    '}
              >
                <div className="text-xs text-gray-600 mb-1"> </div>
                <div className={`text-xl font-bold ${gameCount > 0 ? 'text-red-600' : 'text-gray-400'}`}>
                  {gameCount}
                </div>
              </div>
            </div>
          </div>
        );
      })()}

      {/*  */}
      <div className="mb-6">
        <h3 className="text-xl font-semibold text-gray-800">  </h3>
        <p className="text-sm text-gray-600 mt-1">      .</p>
      </div>

      {/*   */}
      {error && (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
          {error}
        </div>
      )}

      {/*   */}
      {loading && activityData.length === 0 && (
        <div className="text-center py-12">
          <div className="inline-block animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
          <p className="mt-4 text-gray-600">  ...</p>
        </div>
      )}

      {/*  */}
      {!loading || activityData.length > 0 ? (
        <div>
          {renderActivityLogs()}
        </div>
      ) : null}
    </div>
  );
};

export default AdminDatabase;