import React, { memo } from "react";
import TaskItem from "./TaskItem"; // TaskItem   

// props jobs   job   
const JobList = memo(function JobList({
  job, //    (  prop )
  isAdmin, //   (  prop )
  onEditJob, //    ( )
  onDeleteJob, //    ( ) <--  prop 
  onAddTask, //      ( )
  onEditTask, //   
  onDeleteTask, //   
  onEarnCoupon, //   
  addJobTaskButtonStyle, // Dashboard    <--  prop 
}) {
  // job       ( )
  if (!job) {
    return null;
  }

  const isMobile = window.innerWidth <= 768;

  return (
    <div
      key={job.id} //  map   key  X,  
      className="flex flex-col h-full rounded-lg overflow-hidden mb-4"
      style={{
        backgroundColor: "rgba(20, 20, 35, 0.6)",
        border: "1px solid rgba(0, 255, 242, 0.2)",
        boxShadow: "0 4px 15px rgba(0, 0, 0, 0.3)",
        backdropFilter: "blur(5px)",
      }}
    >
      {/*   -   */}
      <div
        className="job-header-container flex justify-between items-center"
        style={{
          background: "linear-gradient(135deg, rgba(79, 70, 229, 0.2) 0%, rgba(99, 102, 241, 0.2) 100%)",
          padding: isMobile ? "12px 14px" : "14px 16px",
          borderBottom: "1px solid rgba(0, 255, 242, 0.2)",
        }}
      >
        <div
          className="job-title-text font-bold"
          style={{
            fontSize: isMobile ? "15px" : "18px",
            textShadow: "0 0 10px rgba(0, 255, 242, 0.3)",
            letterSpacing: "-0.01em",
            color: "#e8e8ff", //  
          }}
        >
          {job.title}
        </div>
        {/*    /  */}
        {isAdmin && (
          <div>
            <button
              onClick={() => onEditJob(job.id)} // job.id  
              className="cursor-pointer p-0 mr-2"
              style={{
                background: "none",
                border: "none",
                color: "rgba(255, 255, 255, 0.7)",
              }}
              aria-label={`${job.title}  `}
            >
              
            </button>
            <button
              onClick={() => onDeleteJob(job.id)} // job.id  
              className="cursor-pointer p-0"
              style={{
                background: "none",
                border: "none",
                color: "rgba(255, 255, 255, 0.7)",
              }}
              aria-label={`${job.title}  `}
            >
              
            </button>
          </div>
        )}
      </div>

      {/*    (flex-grow   ) */}
      <div className="flex-grow overflow-y-auto" style={{ padding: isMobile ? "8px" : "10px" }}>
        {job.tasks && job.tasks.length > 0 ? (
          job.tasks.map((task) => (
            <TaskItem
              key={task.id}
              task={task}
              taskId={task.id}
              jobId={job.id}
              //  task.id  
              onEarnCoupon={onEarnCoupon}
              onEditTask={() => onEditTask(task.id, job.id)} // Dashboard    job.id 
              onDeleteTask={() => onDeleteTask(task.id, job.id)} // Dashboard    job.id 
              isAdmin={isAdmin}
              isJobTask={true}
            />
          ))
        ) : (
          <p
            className="text-center my-2.5"
            style={{
              fontSize: "13px",
              color: "#9999bb",
            }}
          >
              .
          </p>
        )}
      </div>

      {/*      (  ) */}
      {isAdmin && (
        <div style={{ padding: isMobile ? "8px" : "10px", borderTop: "1px solid rgba(0, 255, 242, 0.1)" }}>
          <button
            onClick={onAddTask} // Dashboard job.id    
            // ---       ---
            className="w-full cursor-pointer rounded-md mt-0 transition-all"
            style={
              addJobTaskButtonStyle || {
                /*   ( ) */
                padding: isMobile ? "6px" : "8px",
                backgroundColor: "rgba(0, 255, 242, 0.1)",
                border: "1px solid rgba(0, 255, 242, 0.3)",
                color: "#00fff2",
                fontSize: isMobile ? "12px" : "13px",
                fontWeight: "500",
              }
            }
          >
            +    
          </button>
        </div>
      )}
    </div>
  );
});

export default JobList;
