//      ( )
export const generateJobTaskReward = () => {
  //   (100 ~ 50,000) -    
  const cashRewards = [
    { amount: 50000, weight: 5 },     // 5:  
    { amount: 30000, weight: 10 },    // 3:  
    { amount: 20000, weight: 15 },    // 2:  
    { amount: 10000, weight: 20 },    // 1:  
    { amount: 5000, weight: 18 },     // 5
    { amount: 3000, weight: 12 },     // 3
    { amount: 1000, weight: 10 },     // 1:  
    { amount: 500, weight: 7 },       // 500:  
    { amount: 100, weight: 3 }        // 100:   
  ];

  //   (1, 3, 5, 10, 20)
  const couponRewards = [
    { amount: 20, weight: 10 },       // 20: 10%
    { amount: 10, weight: 20 },       // 10: 20%
    { amount: 5, weight: 20 },        // 5: 20%
    { amount: 3, weight: 20 },        // 3: 20%
    { amount: 1, weight: 30 }         // 1: 30%
  ];

  //     
  const weightedRandom = (items) => {
    const totalWeight = items.reduce((sum, item) => sum + item.weight, 0);
    let random = Math.random() * totalWeight;

    for (const item of items) {
      random -= item.weight;
      if (random <= 0) {
        return item.amount;
      }
    }

    return items[items.length - 1].amount;
  };

  return {
    cash: weightedRandom(cashRewards),
    coupon: weightedRandom(couponRewards)
  };
};
