//   : React.memo 
import React, { memo } from "react";
import { logger } from '../../utils/logger';

const GiftCouponModal = memo(function GiftCouponModal({
  showGiftCouponModal,
  setShowGiftCouponModal,
  recipients,
  giftRecipient,
  setGiftRecipient, //   Dashboard  
  giftAmount,
  setGiftAmount, //   Dashboard   
  handleGiftCoupon,
  currentCoupons,
  userId,
}) {
  if (!showGiftCouponModal) {
    return null;
  }

  const validRecipients = recipients ?? [];

  const closeModal = () => {
    setShowGiftCouponModal(false);
    // ---    ---
    // setGiftAmount setGiftRecipient      
    if (typeof setGiftAmount === "function") {
      setGiftAmount(""); //     
    } else {
      logger.error("setGiftAmount prop is not a function!", setGiftAmount); //   
    }
    if (typeof setGiftRecipient === "function") {
      setGiftRecipient("");
    } else {
      logger.error(
        "setGiftRecipient prop is not a function!",
        setGiftRecipient
      ); //   
    }
  };

  return (
    <div
      className="modal-backdrop fixed inset-0 bg-black/50 flex justify-center items-center z-[1000]"
      onClick={closeModal} //    
    >
      <div
        className="gift-coupon-modal bg-white rounded-xl p-6 w-[90%] max-w-[400px] shadow-xl"
        onClick={(e) => e.stopPropagation()} //      
      >
        <h3 className="mt-0 text-gray-700"> </h3>
        <div className="mb-4 text-gray-500">
          <p>
              : <strong>{currentCoupons}</strong>
          </p>
        </div>
        <div className="form-group mb-4">
          <label className="block mb-2 font-medium text-gray-600">
             
          </label>
          <select
            value={giftRecipient}
            // onChange  setGiftRecipient  
            onChange={(e) =>
              typeof setGiftRecipient === "function"
                ? setGiftRecipient(e.target.value)
                : logger.error("setGiftRecipient is not a function")
            }
            className="w-full p-2.5 border border-gray-300 rounded-md text-sm"
          >
            <option value="">  </option>
            {validRecipients
              .filter((recipient) => recipient.id !== userId)
              .map((recipient) => (
                <option key={recipient.id} value={recipient.id}>
                  {recipient.name} ({recipient.id})
                </option>
              ))}
          </select>
        </div>
        <div className="form-group mb-6">
          <label className="block mb-2 font-medium text-gray-600">
              
          </label>
          <input
            type="number"
            value={giftAmount}
            // onChange  setGiftAmount  
            onChange={(e) =>
              typeof setGiftAmount === "function"
                ? setGiftAmount(e.target.value)
                : logger.error("setGiftAmount is not a function")
            }
            placeholder="   "
            className="w-full p-2.5 border border-gray-300 rounded-md text-sm"
            max={currentCoupons}
            min="1"
          />
        </div>
        <div className="modal-actions flex justify-end gap-2.5">
          <button
            className="cancel-button px-4 py-2 bg-gray-100 text-gray-600 border-0 rounded-md cursor-pointer font-medium"
            onClick={closeModal}
          >
            
          </button>
          <button
            className="confirm-button px-4 py-2 bg-emerald-500 text-white border-0 rounded-md text-sm font-medium"
            onClick={handleGiftCoupon}
            disabled={
              !giftRecipient ||
              !giftAmount ||
              parseInt(giftAmount) <= 0 ||
              parseInt(giftAmount) > currentCoupons
            }
            style={{
              cursor:
                !giftRecipient ||
                !giftAmount ||
                parseInt(giftAmount) <= 0 ||
                parseInt(giftAmount) > currentCoupons
                  ? "not-allowed"
                  : "pointer",
              opacity:
                !giftRecipient ||
                !giftAmount ||
                parseInt(giftAmount) <= 0 ||
                parseInt(giftAmount) > currentCoupons
                  ? 0.7
                  : 1,
            }}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
});

export default GiftCouponModal;
